<template>
    <div class="yingyong">
        <headerBar :title="headerNavData"></headerBar>
        <yd-cell-item arrow @click.native="contryCodeShow=true">
            <span slot="left">{{itemData.name}}</span>
            <span slot="right">{{itemData.ngname}}</span>
        </yd-cell-item>
        <div class="contrybox">
          <yd-flexbox>
            <div class="contryphone"><input type="text" v-model="contryphone"></div>
            <yd-flexbox-item class="phone">
              <input type="text" :placeholder="$t('data.correctphonenumber')" v-model="phone">
            </yd-flexbox-item>
            <div class="sendCode_btn">
              <yd-sendcode 
                    v-model="start1" 
                    :init-str="$t('data.verificationcode')"
                    @click.native="sendCode1" 
                    type="danger"
                ></yd-sendcode>
            </div>
        </yd-flexbox>
        </div>
        <yd-cell-group>
            <yd-cell-item>
                <yd-input slot="right" type="number" v-model="vaildateCode" :show-clear-icon="false" :show-success-icon="false" :show-required-icon="false" :placeholder="$t('data.Pleaseverificationcode')"></yd-input>
            </yd-cell-item>
      </yd-cell-group>
      <div class="bind_btn" v-if="!headerNavData.left">
        <button type="button" class="btn_c btn_cbj1" @click="goindex()">{{$t('data.skipbtn')}}</button>
        <button type="button" class="btn_c btn_cbj" @click="bindphone()">{{$t('data.bindphonebtn')}}</button>
      </div>
      <button v-else type="button" class="btn_c1 btn_cbj" @click="bindphone()">{{$t('data.bindphonebtn')}}</button>


      <!-- 国家代码 -->
        <div class="contryCodeBox" v-if="contryCodeShow">
          <yd-navbar title="请选择国家" :fixed="true" :bgcolor="headerNavData.bj" fontsize=".36rem" color="#ffffff" height=".9rem">
            <div slot="left" @click="contryCodeShow=false">
                <yd-navbar-back-icon color="#ffffff"></yd-navbar-back-icon>
            </div>
        </yd-navbar>
          <div class="mysearch12">
            <yd-search v-model="search" :on-cancel="cancelHandler" :on-submit="CheckCountryCodeName"
              placeholder="请输入国家名称中文，英文，或者国家代码"></yd-search>
          </div>
          <div class="dresslist">
            <yd-cell-group>
                <yd-cell-item v-for="item in list" @click.native="getItem(item)">
                    <span slot="left">{{item.zhname}} [ {{item.ngname}} ]</span>
                    <span slot="right">{{item.code}}</span>
                </yd-cell-item>
            </yd-cell-group>
          </div>
        </div>
    </div>
  
</template>

<script>
  import {getCookie,setCookie} from '../assets/js/cookie.js'
  import data from '@/api/CountryCodes'
  import headerBar from './header.vue'
export default {
  components:{
      "headerBar":headerBar
  },
  data () {
    return {
      phone:'',
      vaildateCode:'',
      start1: false,
      contryphone:'+86',
      contryCodeShow:false,
      search:'',
      headerNavData:{
         left:true,
         title:this.$t('code.bindphone'),
         right:false,
         bj:'#2c94ed',
         search:false,
         share:false
      },
      itemData:{
        name:'请选择国家',
        ngname:''
      },
      list:data
    }
  },
  created(){
    this.list = data
    var org = this.$route.query.org
    if(org){
      this.headerNavData.left = false
    }else{
      this.headerNavData.left = true
    }
    if(this.$route.query.phone){
      this.phone = this.$route.query.phone
    }
  },
  methods:{
    //取消搜索
    cancelHandler(){
      this.search = ""
      this.list = data
    },
    //提交搜索
    CheckCountryCodeName(){
      var that = this
      var searchName = this.search
      var oldlist = this.list
      var newlist=[]
      if(searchName==""){
        this.list = data;
        return;
      }
      data.forEach(item => {
        if(item.zhname.indexOf(searchName) >=0 || item.ngname.toLowerCase().indexOf(searchName.toLowerCase()) >=0 || item.code.indexOf(searchName) >= 0){
          newlist.push(item)
          this.list = newlist
        }
      });
    },
    //选择国家
    getItem(item){
      this.contryphone="+"+item.code
      this.itemData.name = item.zhname
      this.itemData.ngname = item.ngname
      this.contryCodeShow=false
    },
    //绑定电话号码
    bindphone(callback){
      var that = this
      var reg = /^1[3|4|5|6|7|8|9][0-9]\d{4,8}$/
      var phone = this.phone
      var vaildateCode = this.vaildateCode
      if(this.phone==""){
        this.Toast(that.$t('data.correctphonenumber'))
        return;
      }else if(this.vaildateCode==""){
        this.Toast(that.$t('data.Pleaseverificationcode'))
        return;
      } else{
        that.axios({
            url:that.Url + 'Wallet.ashx',
            method:'post',
            params: {req:6,publickey:that.$route.query.publickey,phone:phone,vaildateCode:vaildateCode}
          }).then(function (res) {
              var _list = res.data;
              that.Toast(_list.Msg)
              if(res.data.Success){
                setTimeout(function(){
                  that.$router.replace({path:'/'})
                },1500)
              }
              
          })
      }
      
    },
    //发送验证码
    sendCode1() {
      var that = this
      var reg = /^1[3|4|5|6|7|8|9][0-9]\d{4,8}$/
      var phone = this.phone
      var newPhone = this.contryphone + this.phone
      if(this.contryphone=="+86"){
        if(!reg.test(phone)){
          this.Toast(that.$t('data.correctphonenumber'))
          return;
        }else{
          this.SendCodeMessage(newPhone)
        }
      }else{
        this.SendCodeMessage(newPhone)
      }
    },
    //发送验证码
    SendCodeMessage(newPhone){
      var that = this
      that.axios({
            url:that.Url + 'ShortMessaging.ashx',
            method:'post',
            params: {req:6,phone:newPhone,sign:that.myMd5(newPhone + that.mykey)}
          }).then(function (res) {
              var _list = res.data;
              if(_list.Success){
                that.$dialog.loading.open(that.$t('data.Sending'));
                setTimeout(() => {
                    that.start1 = true;
                    that.$dialog.loading.close();
                    that.$dialog.toast({
                        mes: that.$t('data.overSending'),
                        icon: 'success',
                        timeout: 1500
                    });
                }, 1000);
              }else{
                that.Toast(_list.Msg)
              }
          })
    },
    goindex(){
      this.$router.push({path:'/',query:{ state:1}})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.yingyong{
  padding-top: .9rem;
  font-family: -apple-system, BlinkMacSystemFont, "PingFang SC","Helvetica Neue",STHeiti,"Microsoft Yahei",Arial,Tahoma,Simsun,sans-serif;
}
.yd-cell-item{
  border-bottom: 1px solid #f4f4f4;
}
.yd-checkbox-text{
  display: none;
}
.reda{
  padding: 0 0 0 .28rem;
  font-size: .26rem;
  color: #666;
}
.reda span{
  color: #278eed
}
.btn_c{
  margin: .35rem .28rem .15rem;
  height: .7rem;
  line-height: .7rem;
  text-align: center;
  width: calc(40% - .56rem);
  border: none;
  background: #cdcdcd;
  font-size: .28rem;
  color: #fff;
  border-radius: 5px;
}
.btn_c1{
  margin: .35rem .28rem .15rem;
  height: .7rem;
  line-height: .7rem;
  text-align: center;
  width: calc(100% - .56rem);
  border: none;
  background: #cdcdcd;
  font-size: .28rem;
  color: #fff;
  border-radius: 5px;
}
.btn_d{
  margin: 0 .28rem;
  height: .7rem;
  line-height: .7rem;
  text-align: center;
  width: calc(100% - .56rem);
  border: none;
  font-size: .28rem;
  color: #278eed;
  border-radius: 5px;
}
.btn_cbj{
  background: #277ded!important;
}
.btn_cbj1{
  background: #c5c5c5!important;
}
.createf{
  background: #f4f4f4;
  padding: .2rem .35rem; 
  font-size: .26rem;
  color: #278eed
}
.bind_btn{
  text-align: center
}
.contryphone input{
  height: 1rem;
  width: 70px;
  border: none;
  padding: 0 10px;
  border-right: 1px solid #f4f4f4;
  outline: none;
  font-size: .3rem;
  text-align: center
}
.phone input{
  height: 1rem;
  border: none;
  outline: none;
  padding: 0 10px;
  font-size: .3rem;
}
.contrybox{
  border-bottom: 1px solid #f4f4f4;
}
.sendCode_btn{
  padding-right: 10px;
}
.contryCodeBox{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  z-index: 99999;
  background: #fff;
}
.mysearch12{
  position: fixed;
  top: .9rem;
  left: 0;
  width: 100%;
  z-index: 9999;
}
.dresslist{
  padding-top: 1.9rem;
}
</style>



